import React, { Component } from 'react';
import {Link} from 'react-router'
import './Prevencao.css'

export default class Prevencao extends Component{

    constructor(){
        super()
        this.state = {
            prevencao: '',
            menu: true
        }

        this.clickPrevencao = this.clickPrevencao.bind(this)
    }

    setCuidadosGerais(){
        this.setState({menu: false, prevencao:'cuidadosGerais'})
    }

    setTecnicas(){
        this.setState({menu: false, prevencao:'tecnicas'})
    }

    setOrientacoes(){
        this.setState({menu: false, prevencao:'orientacoes'})
    }

    setRetiradaLeite(){
        this.setState({menu: false, prevencao:'retiradaLeite'})
    }

    setAcessorios(){
        this.setState({menu: false, prevencao:'acessorios'})
    }

    setAspectos(){
        this.setState({menu: false, prevencao:'aspectos'})
    }

    setContent(){
        if(this.state.prevencao){
            return this[this.state.prevencao]()
        }
    }

    clickPrevencao(event){
        event.preventDefault()
        this.setState({menu:true, prevencao:''})
    }

    render(){
        return (
            <main className="container-fluid">
                <div className="row justify-content-md-center">
                    <div className="col-8">
                        <h2 className="title-page"><Link to="/prevencao" onClick={this.clickPrevencao}>Preveno</Link><hr/></h2>
                        {
                            this.state.menu ? this.showMenu() : ''
                        }

                        {
                            this.setContent()
                        }
                    </div>
                </div>
            </main>
        )
    }

    showMenu(){
        return (<div>
            <div className="row container-cards">
                <div onClick={this.setCuidadosGerais.bind(this)} className="col card">
                    <p>Cuidados Gerais com as Mamas e Mamilos</p>
                </div>
                <div onClick={this.setTecnicas.bind(this)} className="col card">
                    <p>Tcnicas de Amamentao</p>
                </div>
            </div>

            <div className="row container-cards">
                <div onClick={this.setOrientacoes.bind(this)} className="col card">
                    <p>Orientaes para o processo de amamentao</p>
                </div>
                <div onClick={this.setRetiradaLeite.bind(this)} className="col card">
                    <p>Retirada do leite das mamas</p>
                </div>
            </div>
            
            <div className="row container-cards">
                <div onClick={this.setAcessorios.bind(this)} className="col card">
                    <p>Bicos e outros acessrios</p>
                </div>
                <div onClick={this.setAspectos.bind(this)} className="col card">
                    <p>Aspectos subjetivos</p>
                </div>
            </div>
        </div>)
    }

    aspectos(){
        return (
            <div className="conteudo">
                <p>So bases para o sucesso da amamentao:</p>
                <ul className="listBullet">
                    <li>Observar sinais relacionados ao vnculo me-beb;  </li>
                    <li>Atentar para os primeiros sinais de depresso materna e encaminhamento para profissional especializado; oferecer apoio profissional, bem como estimular o apoio da famlia, amigos e comunidade;  </li>
                    <li>Saber ouvir a me-nutriz sobre seus anseios, dvidas e experincias anteriores faz parte do processo de aprendizado da amamentao, apoiando-a e aumentando sua autoconfiana; observar se a me apresenta sinais de esgotamento fsico e orient-la a descansar e/ou fazer algo prazeroso;  </li>
                    <li>Recordar sempre  nutriz que no ato de amamentar, a me recebe um dos efeitos da ocitocina, que  o aumento da resistncia ao cansao, sentimento de bem-estar e euforia, isto , AMAMENTAR ACALMA; </li>
                </ul>
            </div>
        )
    }

    acessorios(){
        return (
            <div className="conteudo">
                <p>Bicos e outros acessrios</p>
                <ul className="listBullet">
                    <li>Orientar para NO utilizao de bicos (chupetas, mamadeiras, chucas), que podem ser causas de pega incorreta e alteraes no padro de suco, podendo levar  ocorrncia dos traumas mamrios;  </li>
                    <li>Avaliar cada caso em relao  utilizao de Conchas, absorventes de seios, bicos de silicone (avaliar a real necessidade desses itens). As conchas e os absorventes de seios, quando mantidos midos, podem levar a infeces, tais como a cndida, por exemplo, que se desenvolve em meio mido e quente, o que, por sua vez, pode acarretar o desmame em decorrncia da dor ou desconforto da leso mamilar.  </li>
                    <li>Obs. Existem no mercado itens como intermedirio de silicone, conchas de amamentao, absorventes de seio, placas de hidrocoloide (mamare). No entanto, no existem evidncias cientficas suficientes para indicar o uso desses dispositivos. Nesse sentido, cada caso deve ser criteriosamente avaliado, levando-se em conta os riscos e benefcios de cada uma dessas possibilidades. </li>
                </ul>                
            </div>
        )
    }

    retiradaLeite(){
        return (
            <div className="conteudo">
                <h4>Retirada do Leite</h4>
                <p>EXTRAO DO LEITE MATERNO</p>
                <ul className="listBullet">
                    <li>Orientar a me para extrair, QUANDO:  </li>
                    <li>Leite em excesso (equilbrio da produo do LM e demanda do recm-nascido);  </li>
                    <li>Mamas trgidas e cheias, com ou sem dor  palpao, trgidas e cheias com pontos de ingurgitamento;  </li>
                    <li>Quando me e o beb no podem ficar juntos;  </li>
                    <li>Beb apresenta dificuldade de sugar;  </li>
                    <li>A me deseja doar o excedente de seu leite aos Bancos de Leite (BLH) para ser pasteurizado (LHOP);  </li>
                    <li>Orientar a me sobre como guardar o leite humano ordenhado cru (LHOC): Caso no tenha refrigerador: o leite pode ser coletado em vasilha limpa, fervida durante 15 minutos e colocado em local fresco. Esse leite s deve ser usado at seis horas aps a coleta. Se tiver refrigerador: o leite pode ser refrigerado com segurana por at 24 horas ou congelado por at 30 dias. Antes de alimentar o beb com o leite armazenado, deve-se aquec-lo em banho-maria e oferecer com colher ou copinho exclusivo para o beb, sempre desprezando o que sobrou. </li>
                </ul><br/>
                <p>COMO FAZER A EXTRAO - ORDENHAR:</p>
                <ul className="listBullet">
                    <li>Prender e usar tocas nos cabelos; proteger a boca e o nariz com pano ou fralda;  </li>
                    <li>Lavar as mos e antebrao e sec-los com toalha limpa ou papel;  </li>
                    <li>Escolher um local limpo e tranquilo;  </li>
                    <li>Preparar uma vasilha (de preferncia um frasco com tampa plstica) fervida por 20 minutos;  </li>
                    <li>Massagear o peito com a ponta de dois dedos, indo da regio mais prxima da arola at a mais distante do peito, apoiando-o com a outra mo; massagear por tempo maior as reas mais doloridas; apoiar a ponta dos dedos (polegar e indicador) acima e abaixo da arola, comprimindo o peito contra o trax. Comprimir com movimentos rtmicos, como se tentasse aproximar as pontas dos dedos, sem deslizar na pele para no causar traumas mamrios;  </li>
                    <li>Desprezar os primeiros jatos e guardar o restante no recipiente. </li>
                </ul>
            </div>
        )
    }

    orientacoes(){
        return (
            <div className="conteudo">
                <p>Orientaes para o processo de amamentao</p>
                <ul className="listBullet">
                    <li>Amamentao Livre demanda: Iniciar a amamentao na primeira hora aps o nascimento, estimulando a permanncia em alojamento conjunto. Um beb em aleitamento exclusivo costuma mamar de 8 a 12 vezes por dia, e nos primeiros meses  natural que mame com muita assiduidade e sem horrios e perodos regulares. O importante  que a criana esgote a mama, de modo a receber tanto o leite do comeo quanto o do fim da mamada, por ser mais calrico, promover maior saciedade e ganho de peso adequado. A prxima mamada deve, sucessivamente, comear pela ltima mama oferecida.  </li>
                    <li>Adoo de tcnicas corretas de amamentao; evitar o uso de complementos (gua, chs e substitutos do leite humano).  </li>
                    <li>DURANTE E ENTRE AS MAMADAS: Quando as mamas esto cheias e rgidas:  </li>
                    <li>Proceder ao esvaziamento parcial das mamas sempre que a me sentir dor  palpao, o que pode ser antes das mamadas ou proceder com a ordenha manual entre as mamadas, deixando a arola macia e flexvel, facilitando uma BOA PEGA. Do contrrio (no promoo da flexibilidade mamiloareolar), o beb pode no retirar a quantidade de leite que necessita, comprometendo o ganho de peso, provocando leses de mamilos e a mamada pode se tornar dolorosa.  </li>
                    <li>Quando a me tem a sensao de mamas vazias. Nos primeiros dias aps o parto as mamas podem parecer vazias, passando a impresso errnea  me de que ela no est produzindo leite suficientemente. Algum colostro j est pronto quando o beb nasce, sendo produzindo em quantidade crescente at que ocorra a apojadura, por volta do 2 ao 7 dia, enquanto isso o beb recebe o colostro (de 5 a 20 ml por mamada, atendendo s suas necessidades), sendo substitudo gradativamente pelo leite de transio. O importante  orientar a me para colocar o beb para sugar no seio com maior frequncia; massagear as mamas delicadamente e manter estmulo atravs da expresso mamiloareolar.  </li>
                    <li>TMINO DA MAMADA: No interromper a mamada, porque  importante dar de mamar at o beb soltar (leite posterior). Caso o beb no solte a mama, usar a TCNICA DO DEDO MNIMO: a me pode colocar o dedo mnimo na boca do lactente pela comissura labial, evitando que o recm-nascido puxe o mamilo com fora para largar o peito (como s vezes  difcil o beb soltar, uma dica  dizer  me que deixe o beb pegar seu dedo para soltar na mama)  prevenindo as leses mamrias. </li>
                </ul>
            </div>
        )
    }

    tecnicas(){
        return (
            <div className="conteudo">
                <h5>Tcnicas de amamentao</h5><br/>
                <p>Posicionamento da dupla me-beb:</p><br/>

                <ul className="listBullet">
                    <li>Posio deitada: Nesta posio a me se coloca de costas e fica apoiada por travesseiros.  adotada geralmente por mes no ps-operatrio imediato - POT- ou em outra situao. Pode ficar deitada de lado: Com cabea e costas apoiadas em travesseiros, o beb fica de frente e voltado para me e sua cabea apoiada no brao da me, em travesseiros ou mesmo na cama elevada. Esta posio ajuda as mes com mamas grandes ou pequenas, pois ao encostar-se na cama, fica mais fcil para o beb abocanhar a mama. Tambm pode ser utilizada quando a me estiver cansada. Observar se o beb est com a cabea ligeiramente elevada, evitando possveis infeces da orelha mdia da criana;  </li>
                    <li>Posio sentada com o beb na posio tradicional: me sentada numa cadeira, poltrona ou cama, com as costas apoiadas confortavelmente no encosto, seja em travesseiros ou almofadas, de modo a no ficar curvada para frente e nem para trs. Os ps apoiados de preferncia acima do nvel do cho. O beb, tambm relaxado e confortvel, dever ser colocado diagonalmente em relao ao corpo da me - de abrao-, beb de frente, abdome com abdome, acomodando uma mozinha no seio e a outra tocando o trax lateral da me. Corpo e pescoo alinhados e o ltimo levemente estendido, devendo a boca ficar de frente para o mamilo. A me apoia e oferece a mama em forma de C e com a outra mo segura o beb, amparando sua ndega;  </li>
                    <li>Posio sentada com beb na posio invertida ou posio de bola de futebol americano: Sugerida s mes com mamas grandes, com mamilos malformados ou mulheres submetidas  cesariana e para melhor esvaziamento das mamas em casos de ductos obstrudos, para bebs que tem preferncia por s um peito, ou para amamentar gemelares ao mesmo tempo. Nesta posio a me fica sentada confortavelmente e o beb  apoiado por travesseiros, pois ela colocar o corpo da criana na posio inversa, debaixo dos seus braos, com as pernas do beb voltadas para trs do corpo materno, de forma que o corpo do beb fique encostado em suas costelas. Cabea apoiada na mo da me com mobilidade para lev-lo ao seio. Essa posio facilita a pega e  ortosttica, pois a cabea do beb fica posicionada para cima e para frente;  </li>
                    <li>Posio sentada com o beb na posio de cavaleiro: Esta posio pode ser usada para bebs com refluxo gastresofgico, crianas com fissura labial e/ou palatina. A me deve ficar relaxada, sentada ou levemente inclinada para frente. O beb, sentado no colo, com as perninhas abertas uma de cada lado na coxa da me. Com uma mo a me sustenta a cabea, pescoo e tronco do beb e, com a outra, apoia a mama com a palma de sua mo e com os dedos mdio, anular e mnimo, pressionando-a e comprimindo-a para dentro da boca do beb. O queixo do beb poder ser apoiado com o indicador e o polegar, formando um arco na posio de mo bailarina. Ao mesmo tempo, a me tampona a fenda labial e a gengiva com a poro digital do polegar, deixando as narinas livres para que o beb possa respirar. Dessa maneira, ele consegue formar o vcuo necessrio para amamentar, mas se esse processo o cansar, fazendo com que ele no extraia o leite posterior, a me deve realizar a ordenha e ofertar com o auxlio de colher ou copinho  </li>
                    <li>Posio Descontrada (laid-back position): Posio semideitada, levemente reclinada, relaxada, com ombros, cabea e pescoo bem apoiados. O beb fica em cima da me, na posio longitudinal ou oblqua.  </li>
                    <li>Obs.: Em todas as posies - Me sempre segura o seio em C e/ou mo de bailarina - NO na forma de tesoura.  </li>
                </ul><br/>

                <p>Pega e suco do beb:</p><br/>

                <ul className="listBullet">
                    <li>A produo apropriada de leite vai depender predominantemente da suco do beb (que estimula a produo de prolactina). No entanto, para a ejeo do leite (necessita da fabricao de ocitocina), sendo esta, naturalmente influenciada pela condio emocional da me (autoconfiana)  </li>
                    <li>O beb deve ser levado at a mama e no ao contrrio  estmulo do reflexo de busca e apreenso, quando algo tocar nos lbios ou bochechas do beb ele abre a boca, pe a lngua para baixo e para fora, virando a cabea a procura do que tocou nele. Caso isso no ocorra, deve-se puxar o queixo do recm-nascido para baixo com o polegar ou indicador;  </li>
                    <li>Sinais de uma boa pega e suco efetiva: a criana deve abocanhar no s o mamilo, mas principalmente toda ou a maior parte da arola. Boca bem aberta, com lbio inferir retidos para fora  boca tipo peixinho; lngua acoplada em torno do seio; bochechas arredondadas; mais arola acima da boca do beb; A criana deve parecer tranquila com suco lenta, profunda e ritmada e com perodos de atividade e pausa; mamada indolor.  </li>
                    <li>Quando o seio toca o palato do beb, desencadeiam-se o Reflexo de suco- os ductos lactferos terminais, situados abaixo da arola, so pressionados pela lngua contra o palato, iniciando-se a sada do leite. </li>
                    <li>Obs.: Orientar a me a observar sinais de ejeo de leite, como o vazamento da mama contralateral e a presena das contraes uterinas.  </li>
                    <li>- A lngua do beb em torno do seio, forma de concha, a boca do beb se enche de leite, sendo desencadeado o Reflexo de deglutio, que exige coordenao entre a deglutio e respirao  o beb engole a cada uma a quatro suces, sendo que este movimento de deglutio pode ser observado ou auscultado.  </li>
                    <li>- Sinais de uma m pega/suco no efetiva: boca pouco aberta; lbio inferior voltado para dentro; lngua do beb no visvel; bochechas tensas ou para dentro (covinhas); mais arola abaixo da boca do beb; suco rpidas; ouve-se rudos altos ao sugar (estalidos); dor ou ardor ao amamentar.  </li>
                    <li>Obs.: Encaminhar a me-nutriz para assistncia de profissional ou centro especializado (por ex. bancos de leite, hospitais amigo da criana), quando h padro anormal de suco do recm-nascido ou outra condio do RN que merece cuidados especializados. </li>
                </ul>
            </div>
        )
    }

    cuidadosGerais(){
        return (
            <div className="conteudo">
                <h6>Cuidados com as MAMAS perodo gravdico-puerperal:</h6><br/>
                <p>Cabe aos profissionais de sade envolvidos no universo materno-infantil, ORIENTAR:</p>

                <ul className="listBullet">
                    <li>Tcnicas de amamentao: Pega/suco e posicionamento adequado da dupla me-beb;  </li>
                    <li>Manter os mamilos secos, expondo-os ao ar livre banho de sol (at as 10 horas da manh ou aps as 16 horas, durante 15 minutos);  </li>
                    <li>Uso de suti adequado desde a gestao (neste momento, as mamas j apresentam o primeiro aumento de volume). O suti deve ter tamanho apropriado, alas largas, sem aro e ser de tecido de algodo. Uma dica  o uso de tops de algodo nos primeiros dias, uma vez que so mais confortveis, sustentam e so de tecido leve, permitindo a respirao da pele;  </li>
                    <li>Desestimular o uso de cremes, sabo, sabonete, leos e pomadas, atos de esticar os mamilos com os dedos, esfreg-los com buchas ou toalhas speras (na maioria das vezes essas estratgias no funcionam e podem ser prejudiciais);  </li>
                    <li>Evitar a remoo do colostro durante a gestao, pois pode estimular as contraes uterinas;  </li>
                    <li>A criana mama a arola e no o bico, de modo que nenhum tipo de MAMILO impede a amamentao, mesmo os semiprotrusos, pseudoinvertidos e invertidos, que podem dificultar a amamentao no incio. Com uma BOA PEGA (de toda a arola) e o apoio de um profissional habilitado,  possvel amamentar;  </li>
                    <li>ATENO maior s mes que possuem implantes de silicone ou realizaram mamoplastia. SEMPRE OBSERVAR CADA CASO, evitando ideias pr-concebidas de que a me no pode amamentar. Cada mulher  nica e deve ser considerada em sua singularidade.  </li>
                    <li>HIGIENIZAO DAS MAMAS: Evitar o uso de produtos que retiram a proteo natural do mamilo, como sabo, lcool ou qualquer produto secante. Limpar as mamas somente com gua durante a higienizao corporal e aplicar o prprio leite materno aps as mamadas, deixando secar naturalmente.  </li>
                    <li>VESTIMENTAS: As roupas da me e do beb devem ser adequadas, sem restringir os movimentos. Recomenda-se que as mamas estejam expostas sempre que possvel e o beb vestido de maneira que os braos fiquem livres. </li>
                </ul>
            </div>
        )
    }
}
